.footer {
  height: 360px;
  position: relative;
  background-image: url("../images/footer.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  &__logo {
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: auto;
  }
}
